/* Asegurar que todo se ajuste bien en cualquier pantalla */
* {
  box-sizing: border-box;
  max-width: 100%;
}

/* Ajuste del `body` para que no haya overflow en móviles */
body {
  overflow-x: hidden;
}

/* Asegurar que imágenes y videos sean completamente responsivos */
img, video, iframe {
  max-width: 100%;
  height: auto;
}

/* Ajustes de `html` para tamaños de fuente escalables */
html {
  font-size: 16px; /* Base */
}

@media (max-width: 1200px) {
  html { font-size: 14px; } /* Reducir tamaño en pantallas más pequeñas */
}

@media (max-width: 768px) {
  html { font-size: 12px; } /* Reducir más para móviles */
}

/* 🔹 Navbar */
.navbar {
  flex-wrap: wrap; /* Permite que los elementos se acomoden si no caben */
}

/* 🔹 Ocultar enlaces en móviles y mostrar menú hamburguesa */
.menu-toggle {
  display: none; /* Solo visible en móviles */
  font-size: 24px;
  cursor: pointer;
}

/* 🔹 Ajustes para móviles */
@media (max-width: 768px) {
  .nav-links {
    display: none; /* Oculta los enlaces */
    flex-direction: column;
    width: 100%;
    text-align: center;
    position: absolute;
    top: 60px;
    left: 0;
    background: #222;
    padding: 10px 0;
  }

  .nav-links.show {
    display: flex; /* Muestra el menú cuando se activa */
  }

  .menu-toggle {
    display: block; /* Se muestra en móviles */
  }
}

